.tech__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center; 

    padding: 2rem 6rem;
}

.tech__navbar-links { 
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.tech__navbar-links-logo {
    margin-right: 2rem;
}
.tech__navbar-links-logo img {
    width: 110.56px;
    height: 25.02px;
}

.tech__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.tech__navbar-sign {
    display: flex;
    justify-self: flex-end;
    align-items: center;
}

.tech__navbar-links_container p,
.tech__navbar-sign p,
.tech__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    
    margin: 0 1rem;
    cursor: pointer;
}

.tech__navbar-sign button, 
.tech__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;    
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.tech__navbar-menu {
    margin-left: 1rem;

    
    position: relative;
}

.tech__navbar-menu svg{
    cursor: pointer;
}

.tech__navbar-menu_container {
   display: flex;
   justify-content: flex-end;
   align-items: flex-end;
   flex-direction: column;

   text-align: end;
   background: var(--color-footer);
   padding: 2rem;
   position: absolute;
   right: 0;
   top: 40px;
   margin-top: 1rem;
   min-width: 210rem;
   border-radius: 5px;
   box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.tech__navbar-menu_container p {
    margin: 1rem 0;
}
.tech__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .tech__navbar-links_container {
        display: none;
    }
    .tech__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .tech__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .tech__navbar {
        padding: 2rem ;
    }
    .tech__navbar-sign{
        display: none;
    }
    .tech__navbar-menu_container {
        top: 20px;
    }

    .tech__navbar-menu_container-links-sign {
        display: block;
    }
}